function buildChartOptions() {
  const options = {
    responsive: true,
    legend: {
      display: false
    },
    title: {
      display: false
    },
    tooltips: {
      mode: "index",
      intersect: false
    },
    hover: {
      mode: "nearest",
      intersect: true
    },
    scales: {
      xAxes: [
        {
          display: true,
          scaleLabel: {
            show: true
          }
        }
      ],
      yAxes: [
        {
          display: true,
          scaleLabel: {
            show: true
          }
        }
      ]
    }
  };
  return options;
}

function TabContainer(props) {
  return (
    <Typography component="div" style={{ padding: 8 * 3 }}>
      {props.children}
    </Typography>
  );
}

