import * as React from "react";
import axios from "axios";
import { withStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import { Line } from "react-chartjs-2";
import colors from "../constants/colors";
import { Typography } from "@material-ui/core";
import Tabs from "./Tabs";

const styles = theme => ({
  root: {
    flexGrow: 1,
    margin: "10px",
    marginTop: "80px"
  }
});

class FullWidthGrid extends React.Component {
  state = {
    errors: [],
    successes: []
  };

  componentDidMount() {
    this.getResults();
  }

  getResults = async () => {
    try {
      const res = await axios.get("/results", {
        params: {
          api_key: "VMVK61C-1VSM6QY-HZV19R6-AXE5657"
        }
      });
      const results = res.data;
      const errors = results.filter(result => result.error);
      const successes = results.filter(result => !result.error);
      this.setState({
        errors,
        successes
      });
    } catch (err) {}
  };

  render() {
    const { errors, successes } = this.state;
    const { classes } = this.props;
    return (
      <div className={classes.root}>
        <Grid container spacing={24}>
          <Grid item xs={12} sm={6} style={{}}>
            <Typography variant="h6" color="inherit" gutterBottom={true}>
              Success Rate
            </Typography>
            <Tabs color={colors.info} results={successes} />
          </Grid>
          <Grid item xs={12} sm={6} style={{}}>
            <Typography variant="h6" color="inherit" gutterBottom={true}>
              Error Rate
            </Typography>
            <Tabs color={colors.error} results={errors} />
          </Grid>
        </Grid>
      </div>
    );
  }
}

export default withStyles(styles)(FullWidthGrid);
